import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import { MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBRow, MDBCol, MDBView, MDBIcon } from 'mdbreact';
import { CSS } from "../../assets/style";
import { call } from '../../services/Api';
import CustomBtn from '../CustomBtn';
import ViewMoreCard from './ViewMoreCard';
import ActionButton from '../ActionButton';
import MediaCard from '../Media/MediaCard'
import { connect } from 'react-redux';
import { updateCart } from '../../Actions/ActionTypes'
import { ADMIN } from '../../services/Constants';

const BasicCard = (props: any) => {

    const addToCart = (item: any) => {
        let existingCartItems: Array<any> = [...props.cartItems, item];
        props.updateCart(existingCartItems);
    }

    const removeFromCart = (item: any) => {
        let existingCartItems: Array<any> = [...props.cartItems];
        existingCartItems.splice(existingCartItems.findIndex((existingItem: any) => existingItem._id === item._id), 1);
        props.updateCart(existingCartItems);
    }

    const BookingButton = (item: any) => {
        let cheekIfAlreadyExistInCart: boolean = false;
        let existingCartItems: Array<any> = [...props.cartItems];

        if (existingCartItems.some((existingItem: any) => item._id === existingItem._id)) {
            cheekIfAlreadyExistInCart = true
        }

        return <div className="mt-3 text-right d-flex align-items-center justify-content-end">
            {props.userType === ADMIN ? null :
                cheekIfAlreadyExistInCart ? <Button
                    variant="contained"
                    size="small"
                    color="secondary"
                    onClick={() => { removeFromCart(item) }}>
                    Remove Service
          </Button> : <CustomBtn
                    btnLabel={'Add Service'}
                    handleButtonClick={() => { addToCart(item) }} />
            }
        </div >
    }
    return (
        <>
            <MDBCard narrow>
                <MDBView cascade>
                    <MediaCard {...props} figure />
                </MDBView>

                <MDBCardBody style={{ ...CSS.cardBg }}>
                    {/* <h5 className={`${!props.specialInfo ? 'invisible ' : ''} pink-text`} >
                        <MDBIcon icon='fas fa-hand-holding-heart' /> {props.specialInfo}
                    </h5> */}

                    <MDBCardTitle className='text-white'>
                        {props.name}
                    </MDBCardTitle>

                    <MDBCardText className="text-light">
                        {props.description}
                    </MDBCardText>

                    <h3 className="text-info mb-3 text-center">
                        {props.price}
                    </h3>


                    {BookingButton(props)}

                    {!props.hideActionButton && <ActionButton {...props} />}
                </MDBCardBody>
            </MDBCard>

            {/* {props.isLanding ? <MDBCol className="mt-3" md='3' key="viewMore">
                <ViewMoreCard link="services" />
            </MDBCol> : null} */}
        </ >
    )
}

const mapStateToProps = (state: any) => {
    return {
        userType: state.UserReducer.userDetails.userType,
        cartItems: state.UserReducer.cartItems
    }
}

export default connect(mapStateToProps, { updateCart })(BasicCard);