import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { TransitionProps } from '@material-ui/core/transitions';
import CustomBtn from '../components/CustomBtn';
import { MDBContainer } from 'mdbreact';
import { COLORS, CSS } from '../assets/style';
import BookingStepper from '../components/Bookings/Stepper';
import { connect } from 'react-redux';
import { updateCart } from '../Actions/ActionTypes'
import EmptyCart from '../components/RainyDay/EmptyCart';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Badge, Button } from '@material-ui/core';
import InfoCard from '../components/Card/InfoCard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
        background: COLORS.app_green
    },
    title: {
        flex: 1,
    },
}));

const Transition = React.forwardRef<unknown, TransitionProps>(function Transition(props: any, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


function Booking(props: any) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            {props.cartItems.length === 0 && <CustomBtn btnLabel="Book Appointment" handleButtonClick={handleClickOpen} />}
            {props.cartItems.length > 0 && <IconButton aria-label="cart" color={props.cartItems.length === 0 ? "default" : "inherit"} onClick={handleClickOpen}>
                <Badge badgeContent={props.cartItems.length} color="secondary">
                    <ShoppingCartIcon className="text-white" />
                </Badge>
            </IconButton>}
            <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="Close">
                            <ArrowBackIcon />
                        </IconButton>
                        <Typography variant="h6" color="inherit" className={classes.title}>
                            Your Cart
                        </Typography>
                        <Button color="inherit" onClick={handleClose}>
                            Close
                        </Button>
                    </Toolbar>
                </AppBar>

                <MDBContainer className="h-100 mt-2 p-0 pl-1">
                    {props.cartItems.length === 0 ?
                        <EmptyCart actionCB={handleClose} /> :
                        <>
                            <InfoCard items={props.cartItems} />
                            <BookingStepper hanldeClose={handleClose} />
                        </>
                    }
                </MDBContainer>

            </Dialog>
        </div>
    );
}

const mapStateToProps = (state: any) => {
    return {
        userDetails: state.UserReducer.userDetails,
        cartItems: state.UserReducer.cartItems
    }
}

export default connect(mapStateToProps, { updateCart })(Booking);
