import React from "react";

export default function MediaCard(props: any) {
    return (
        <figure className="text-center shadow m-0">
            {props.figure ? <img src={props.imageUrl} className="figure-img img-fluid z-depth-1 w-100 m-0"
                alt="" style={{ height: '200px' }} /> :
                <iframe style={{ "minHeight": "300px" }} className="w-100 border-0" title="video" src={props.imageUrl} allowFullScreen></iframe>}
        </figure>
    );
}