import React, { useState } from "react";
import InfoDialog from "./Dialog/InfoDialog";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';
import { Button, DialogContentText } from "@material-ui/core";
import AddNew from "./Form/AddNewForm";
import { call } from "../services/Api";
import { updateNotofication } from "../Actions/ActionTypes";

export default function ActionButton(props: any) {
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
    const [errorMessage, serErrorMessage] = useState('');

    const createEditComponent = (formData: any) => {
        return <AddNew {...props} data={formData} cb={props.cb} />
    }

    const deleteItem = (item: any) => {
        setIsApiCallInProgress(true);

        call(`image/${item._id}`, 'DELETE').then((response: any) => {
            setIsApiCallInProgress(false);
            if (response.error) {
                serErrorMessage(response.error)
            } else if (response.deletedCount === 0) {
                serErrorMessage(`Can Not Delete ${item._id}`)
            } else {
                serErrorMessage('');
                updateNotofication({ type: "info", message: `${item._id} Deleted` })
                props.cb();
            }
        })
    }

    const createDeleteComponent = () => {
        return <div className="text-center">
            <DialogContentText className="text-left">
                Are you sure you want to delete {props.title}
                {/* <Error errorMessage={errorMessage} /> */}
            </DialogContentText>
            <Button
                // style={{ ...CSS.btn }}
                variant="outlined"
                onClick={() => { deleteItem(props.id) }}
                color="secondary"
                disabled={isApiCallInProgress}
                autoFocus>
                 {isApiCallInProgress ? "Deleting..." : (errorMessage ? "Retry" : "Yes")}
            </Button>
        </div>
    }

    return <div className="d-flex justify-content-center m-1">
        <InfoDialog
            isIconButton={true}
            iconButton={<EditIcon color="primary" />}
            btnLabel="Edit"
            btnVariant="contained"
            title={`Edit ${props.title || ''}`}
            component={createEditComponent(props)}
        />
        <InfoDialog
            isIconButton={true}
            iconButton={<DeleteForeverIcon color="error" />}
            btnLabel="Delete"
            btnVariant="contained"
            title={`Delete ${props.id || ''}`}
            component={createDeleteComponent()}
        />
    </div >
}