import React from 'react';
import './App.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";

import { Provider } from "react-redux";
import { Router, Switch, Route, withRouter } from "react-router-dom";
import store from "./Reducers";
import { createBrowserHistory } from "history";

import LandingPage from './pages/LandingPage';
import Header from './components/Header';
import AdminPage from './pages/AdminPage';
import Footer from './components/Footer/Footer';
import { PageNotFound } from './pages/PageNotFound';
import ContactUs from './pages/ContactUs';
import AboutUs from './pages/AboutUs';
import Gallery from './pages/Gallery';
import ServicePage from './pages/Services';
import MyAccount from './pages/MyAccount';
import NotificationPopup from './components/Alert/NotificationPopup';
import { AUTH_PASSWORD, AUTH_USERNAME, updateStoreFromCookie, USER_DETAILS } from './Actions/ActionTypes';
import Cookies from 'js-cookie';

let hist = createBrowserHistory();

hist.listen(_ => {
  window.scrollTo(0, 0)
});

function App() {
  updateStoreFromCookie("auth_userName", AUTH_USERNAME, 'auth_userName');
  updateStoreFromCookie("auth_password", AUTH_PASSWORD, 'auth_password');
  if (Cookies.get('userDetails') !== 'undefined' && Cookies.get('userDetails') !== undefined) {
    let userDetails = parseUserDetails(Cookies.get('userDetails'));
    // userDetails.userType = 'admin';
    store.dispatch({
      type: USER_DETAILS,
      userDetails
    })
  }

  return (
    <Provider store={store}>
      <Router history={hist}>
        <Header />
        <div className="mt-10" />
        <Switch>
          <Route exact path="/" component={LandingPage} />
          <Route path="/aboutus" component={withRouter(AboutUs)} />
          <Route path="/gallery" component={withRouter(Gallery)} />
          <Route path="/contactus" component={withRouter(ContactUs)} />
          <Route path="/admin" component={withRouter(AdminPage)} />
          <Route path="/services" component={withRouter(ServicePage)} />
          <Route path="/myaccount" component={withRouter(MyAccount)} />
          <Route path="*" component={AdminPage} />
        </Switch>
        <NotificationPopup />
        <Footer />
      </Router>
    </Provider>
  );
}

function parseUserDetails(userdetials: any) {
  userdetials = userdetials.replace(/%22/g, '"');
  userdetials = userdetials.replace(/%2C/g, ',');
  return JSON.parse(userdetials);
}


export default App;
