import React from 'react';
import { Typography, Button } from '@material-ui/core';
import { default as ErrorImg } from '../../assets/icons/error.svg';
import { Link } from 'react-router-dom';
import { COLORS } from '../../assets/style';

export default function EmptyCart(props: any) {
    return (
        <div className="p-5" style={{ display: "flex", justifyContent: 'center', alignItems: "center", flexDirection: "column" }}>
            <img alt="error" src={ErrorImg} height="60" width="60" />
            <Typography className="mt-3" style={{ fontSize: 20, color: COLORS.app_green }}>Your Cart is Empty</Typography>
            <div className="d-flex text-center mt-5 justify-content-center">
                <Button size="small" variant="outlined" onClick={props.actionCB}>
                    <Link to="/services">Add Services</Link>
                </Button>
            </div>
        </div>
    )
}
