import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import { Button, Hidden, Paper } from '@material-ui/core';
import { default as TestIcon } from '../../assets/icons/warning.svg';
import InfoDialog from '../Dialog/InfoDialog';
import store from '../../Reducers';
import { USER_CART } from '../../Actions/ActionTypes';
import Rupee from '../RuppeSign';
import InfoTable from '../Table/InfoTable';
import { COLORS } from '../../assets/style';

const useStyles = makeStyles({
    root: {
        width: '100%',
        maxWidth: 360,
    },
    title: {
        textAlign: "center",
        color: COLORS.app_green
    },
    pos: {
        marginBottom: 12,
        color: COLORS.app_green,
        fontSize: "30px",
        textAlign: "end"
    },
});

export default function InfoCard(props: any) {
    const classes = useStyles();

    const createInfoComponent = (item: any) => {
        return <InfoTable {...item} />
    }

    const removeFromCart = (test: any) => {
        let existingCartItems: Array<any> = [...props.items];
        existingCartItems.splice(existingCartItems.findIndex((item: any) => item._id === test._id), 1);
        store.dispatch({
            type: USER_CART,
            cartItems: existingCartItems
        })
    }

    return (
        <Card className="mb-3">
            <CardContent>
                <Typography className={classes.title} variant="h5" gutterBottom>
                    Cart Summary
                </Typography>
                <List>
                    {props.items.map((cartItem: any, index: number) => {
                        return <Paper elevation={3} key={index}>
                            <ListItem className="mb-2" style={{ borderLeft: `3px solid yellow` }}>
                                <Hidden smDown>
                                    <ListItemAvatar>
                                        <img alt="icon" src={TestIcon} height="30px" width="30px" />
                                    </ListItemAvatar>
                                </Hidden>
                                <ListItemText primary={cartItem.name}
                                    secondary={
                                        <>
                                            {cartItem.methodology}
                                            <br />
                                            <div className="d-flex flex-row align-items-center">
                                                <InfoDialog btnVariant="text" btnLabel="Details" title={cartItem.name} component={createInfoComponent(cartItem)} />
                                                <Button color="secondary" size="small" onClick={() => { removeFromCart(cartItem) }} >Remove</Button>
                                            </div>
                                        </>} />
                                <ListItemSecondaryAction className="d-flex flex-row align-items-center">
                                    <Rupee /><Typography style={{ color: COLORS.app_green, fontWeight: 700 }}>{cartItem.price}</Typography>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </Paper>
                    })}
                </List>

                <Typography className={classes.pos} color="textSecondary">
                    <small>{`Subtotal (${props.items.length} item${props.items.length > 1 ? 's' : ''})`} : </small>
                    <Rupee />{props.items.reduce((total: number, item: any) => (total + parseInt(item.price)), 0)}
                </Typography>
            </CardContent>
        </Card>
    );
}
